.items {
    height: 1000px;
    width: 800px;
    background-color: white;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 2px 2px 2px rgb(102 102 102 / 0.2);
    border: inset 0 2px 2px 0 rgba(53, 53, 53, 0.1);
    padding: 0 2px 2px 0;
}

.item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 900px;
    width: 800px;
}

.item1 {
    border-right: 1px solid rgb(102 102 102 / 0.3);
    width: 300px;
    box-shadow: 0px 2px 2px 2px rgba(255, 255, 255, 0.4);
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 30px 0 0 30px;
}

.item2 {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100px;
    width: 800px;
    border-bottom: 1px solid rgb(102 102 102 / 0.3);
    box-shadow: 0px 2px 2px 2px rgba(255, 255, 255, 0.4);
}

.item3 {
    border: 1px solid rgb(102 102 102 / 0.3);
    background: linear-gradient(rgba(255,255,255,.5), rgba(255,255,255,.2)), url('../../../Images/chatbackgroud.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 800px;
    display: flex;
    flex-direction: column;
}

.item3 {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.item5 textarea{
    list-style: none;
    margin-right: 20px;
    border-radius: 10px;
    height: 50px;
    width: 300px;
    resize:none;
    background-color: rgb(252, 244, 235);
    box-shadow: 0 3px 3px 0 rgba(255, 255, 255, 0.2);
} 

.item5 {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin:20px
}

.item5 button {
    background-color: #63aae7; 
    border: none;
    color: white;
    padding: 19px 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    object-fit: cover;
    cursor: pointer;
    border-radius: 50%;
    color: black;
  }

.arrow {
    background: #63aae7;
    text-align: center;
    color: #fff;
    padding: 20px;
    margin: 20px;
    border-radius: 5px;
}

.arrow p{
    color: black;
}

.arrow{
    position: relative;
    z-index: 10;
}
.arrow::after {
    content: '';
    width: 0;
    height: 0;
    display: block;
    position: absolute;
    z-index: 10;
    border: 0;
}

.arrow::after {
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    margin-top: -10px;
    top: 50%;
    border-left: 10px solid #63aae7;
    left: auto;
    right: -10px;
}
