.items {
    display: flex;
    box-shadow: 0px 1px 1px 1px rgba(102, 102, 102, 0.2);
    justify-content: space-around;
    flex-direction: column;
    min-height: 200px;
}

.items img {
    width: 100%;
    height: 100%;
    max-width: 70px;
    max-height: 70px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 30px;
    box-shadow: 1px 1px 1px 1px rgba(48, 48, 48, 0.2);
}

.item1 {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin: 0px 40px;
}

.item2 {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin: 0px 40px
}

.item2 button {
    border-radius: 4%;
    background-color: rgb(236, 172, 53);
    border: none;
    color: rgb(49, 49, 49);
    padding: 12px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
}
  
.item2 button {
    box-shadow: 0 2px 5px 0 rgba(255, 255, 255, 0.2), 0 6px 20px 0 rgba(255, 255, 255, 0.19);
}
  
.item2 button:hover {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.24),0 7px 25px 0 rgba(0, 0, 0, 0.24);
}

.item2 input[type=text] {
    width: 130px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px ;
    font-size: 16px;
    background-color: white;
    background-repeat: no-repeat;
    padding: 12px 20px 12px 15px;
    transition: width 0.4s ease-in-out;
}
  
.item2 input[type=text]:focus {
    width: 450px;
}