.item {
    max-height: 1100px;
    height: fit-content;
    width: 800px;
    background-color: white;
    display: flex;
    flex-direction: column;
    overflow: auto;
    text-overflow: ellipsis;
}

.item::-webkit-scrollbar {
    width: 7px;
 }
 
.item::-webkit-scrollbar-track {
     background-color: rgb(243, 242, 242);
     border-radius: 1px;
}
 
.item::-webkit-scrollbar-thumb:hover{
     border-radius: 10px;
     box-shadow: inset 25px 25px rgba(255, 255, 255, 0.7); 
     background-color: rgb(49, 49, 49);
}

.item1 {
    display: flex;
    align-items: center;
    padding: 50px;
    background-color: white;
    box-shadow: 0px 1px 2px 1px rgb(102 102 102 / 0.2);
    background-clip: content-box, padding-box;
    box-shadow: inset 0 50px 0 0 rgb(243, 242, 242);
}


.posts1{
    border-top: 40px solid rgb(243, 242, 242);
    padding: 0;
    margin-top: 2px;

}

.posts::-webkit-scrollbar {
    width: 7px;
    background-color: #f9f9fd;
}