.item ul {
    display: none;
    background-color: #f90;
    position: absolute;
    top: 100%;
    padding: 0px 220px;
}

.item , .item  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.item  {
    border-bottom: 1px solid rgba(160, 160, 160, 0.3);
    height: 40px;
    padding-left: 25px;
    min-width: 470px;
}

.item  li {
    float: left;
    position: relative;
    height: 100%;
}

.item  li a {
    display: block;
    padding: 9px ;
    width: 100px;
    color: rgb(0, 0, 0);
    text-decoration: none;
    text-align: center;
}

.item  ul li {
    float: none;
}

.item  li:hover:not(.active) {
    border: 1px solid rgb(160, 160, 160, 0.3);
    border-bottom: 1px solid rgb(243, 242, 242);
}

.item  ul li:hover {
    background-color: #666;
}

.item  {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 70px 240px 0px 240px;
    margin-top: 0px;
    background-color:rgb(243, 242, 242);
}

.item  li a.active {
    border: 1px solid rgb(160, 160, 160, 0.3);
    border-bottom: 1px solid rgb(243, 242, 242);
    font-weight: 500;
}

.item  li:hover a.active {
    border: 0;
}
