.post1, .post2{
    display: flex;
    flex-direction: row;
    margin: 40px 40px 40px 40px;

}

.post2 {
    display: flex;
    flex-direction: column;
    max-height: 200px;
    margin: 15px 40px 30px 40px;
    overflow: auto;
    text-overflow: ellipsis;
}
.post2 span{
    margin-top: 10px
}

.post img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    object-fit: cover;
    box-shadow: 1px 1px 1px 1px rgba(48, 48, 48, 0.2);
    
}

.post {
    display: flex;
    height: fit-content;
    box-shadow: 0px 1px 1px 1px rgba(102, 102, 102, 0.2);
    justify-content: flex-start;
    flex-direction: row;
}
